import React from "react";
import Row from "muicss/lib/react/row";
import Button from "muicss/lib/react/button";
import Col from "muicss/lib/react/col";
import Select from "muicss/lib/react/select";
import Option from "muicss/lib/react/option";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as navigationTitleActions from "../../actions/navigationTitleActions";
import * as qtsActions from "../../actions/qtsActions";
import { Navigation } from "../../lib";
import encodeQueryData from "../common/encodeQueryData";
import getParameterByName from "../common/getParameterByName";
import MultiSelect from "./MultiSelect";
import AutocompleteSearch from "../common/AutocompleteSearch";

class QTSSearchForm extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      radius: "3",
      min_loan: "",
      max_loan: "",
      property_types: [],
      initialAddress: "",
      bank: "",
      lending_types: [],
      banker: "",
      timeFrame: "6",
      bankNote: "",
      searchType: "",
      toggleAdvancedSearch: false,
      propertyToggleDropdown: false,
      lenderToggleDropdown: false,
      allCheckbox: true,
      autocompleteAddress: "",
      localPropertyTypes: [],
    };

    this.radiusChange = this.radiusChange.bind(this);
    this.handleAddressChange = this.handleAddressChange.bind(this);
    this.propertyToggleDropdown = this.propertyToggleDropdown.bind(this);
    this.lenderToggleDropdown = this.lenderToggleDropdown.bind(this);
    this.formatMinLoan = this.formatMinLoan.bind(this);
    this.formatMaxLoan = this.formatMaxLoan.bind(this);
    this.submitSearch = this.submitSearch.bind(this);
    this.propertyTypeCheckboxChange = this.propertyTypeCheckboxChange.bind(
      this
    );
    this.focusOfOther = this.focusOfOther.bind(this);
    this.showAdvancedSearch = this.showAdvancedSearch.bind(this);
  }

  UNSAFE_componentWillMount() {
    this.setState({
      localPropertyTypes: [
        { list_type_id: 0, value: "All" },
        ...this.props.propertyTypesList,
      ],
      radius: sessionStorage.getItem("qts_radius")
        ? sessionStorage.getItem("qts_radius")
        : getParameterByName("radius")
        ? getParameterByName("radius")
        : "3",
      min_loan: sessionStorage.getItem("qts_min_loan")
        ? sessionStorage.getItem("qts_min_loan")
        : getParameterByName("min_loan")
        ? getParameterByName("min_loan")
        : "",
      max_loan: sessionStorage.getItem("qts_max_loan")
        ? sessionStorage.getItem("qts_max_loan")
        : getParameterByName("max_loan")
        ? getParameterByName("max_loan")
        : "",
      property_types: sessionStorage.getItem("qts_propertyTypes")
        ? sessionStorage.getItem("qts_propertyTypes").split(",")
        : getParameterByName("property_types")
        ? decodeURIComponent(getParameterByName("property_types")).split(",")
        : [],
      initialAddress: sessionStorage.getItem("qts_address")
        ? JSON.parse(sessionStorage.getItem("qts_address")).address
        : getParameterByName("address")
        ? getParameterByName("address")
        : "",
      bank: sessionStorage.getItem("qts_bank")
        ? sessionStorage.getItem("qts_bank")
        : getParameterByName("bank")
        ? getParameterByName("bank")
        : "",
      lending_types: sessionStorage.getItem("qts_lending")
        ? sessionStorage.getItem("qts_lending").split(",")
        : getParameterByName("lending_types")
        ? decodeURIComponent(getParameterByName("lending_types")).split(",")
        : [],
      banker: sessionStorage.getItem("qts_banker")
        ? sessionStorage.getItem("qts_banker")
        : getParameterByName("banker")
        ? getParameterByName("banker")
        : "",
      timeFrame: sessionStorage.getItem("qts_timeFrame")
        ? sessionStorage.getItem("qts_timeFrame")
        : getParameterByName("date_range")
        ? getParameterByName("date_range")
        : "6",
      bankNote: sessionStorage.getItem("qts_bankNote")
        ? sessionStorage.getItem("qts_bankNote")
        : getParameterByName("note_search")
        ? decodeURIComponent(getParameterByName("note_search")).replace(
            /\+/g,
            " "
          )
        : "",
      searchType: sessionStorage.getItem("qts_searchType")
        ? sessionStorage.getItem("qts_searchType")
        : getParameterByName("search_type")
        ? getParameterByName("search_type")
        : "",
    });
  }

  showAdvancedSearch() {
    this.setState({ toggleAdvancedSearch: !this.state.toggleAdvancedSearch });
  }

  propertyToggleDropdown() {
    let show = !this.state.propertyToggleDropdown;
    this.setState({ propertyToggleDropdown: show }, function () {
      if (show) {
        if (this.state.lenderToggleDropdown) {
          this.setState({ lenderToggleDropdown: false });
        }
      }
    });
  }

  propertyTypeCheckboxChange(selectedOptions) {
    this.setState({ property_types: selectedOptions });
  }

  handleAddressChange(value) {
    this.setState({
      initialAddress: value.formatted_address,
      autocompleteAddress: value,
    });
  }

  focusOfOther() {
    this.setState({
      propertyToggleDropdown: false,
      lenderToggleDropdown: false,
    });
  }

  formatMinLoan(e) {
    var value = e.target.value;
    value = value.toString();
    value = value.replace(/[^0-9]/g, "");
    this.setState({
      min_loan: value ? value.replace(/\B(?=(\d{3})+(?!\d))/g, ",") : "",
    });
  }

  formatMaxLoan(e) {
    var value = e.target.value;
    value = value.toString();
    value = value.replace(/[^0-9]/g, "");
    this.setState({
      max_loan: value ? value.replace(/\B(?=(\d{3})+(?!\d))/g, ",") : "",
    });
  }

  radiusChange(e) {
    this.setState({
      radius: e.target.value,
    });
  }

  timeFrameChange(e) {
    this.setState({ timeFrame: e.target.value });
  }

  bankNoteChange(e) {
    this.setState({ bankNote: e.target.value });
  }

  searchTypeChange(e) {
    this.setState({ searchType: e.target.value });
  }

  formatBanker(e) {
    this.setState({ banker: e.target.value });
  }

  formatBank(e) {
    this.setState({ bank: e.target.value });
  }

  lenderToggleDropdown() {
    let show = !this.state.lenderToggleDropdown;
    this.setState({ lenderToggleDropdown: show }, function () {
      if (show) {
        if (this.state.propertyToggleDropdown) {
          this.setState({ propertyToggleDropdown: false });
        }
      }
    });
  }

  lenderTypeCheckboxChange(selectedOptions) {
    if(selectedOptions.length){
      this.setState({ searchType: 'ltr' });
    }
    this.setState({ lending_types: selectedOptions });
  }

  formatBankNote(note) {
    let searchWords = note.match(/\w+/g);
    let bankNote = "";
    searchWords.forEach((elem, i) => {
      if (searchWords.length - 1 === i) {
        bankNote += elem;
      } else {
        bankNote += elem + "+";
      }
    });
    return bankNote;
  }

  submitSearch() {
    var localAddress = "";
    try {
      localAddress = JSON.parse(sessionStorage.getItem("qts_address"));
    } catch (err) {
      alert("there is no session storage with an address");
    }
    var latitude = this.state.autocompleteAddress.geometry
      ? this.state.autocompleteAddress.geometry.location.lat()
      : localAddress.latitude;
    var longitude = this.state.autocompleteAddress.geometry
      ? this.state.autocompleteAddress.geometry.location.lng()
      : localAddress.longitude;
    var inNYCObject = this.state.autocompleteAddress.address_components
      ? this.state.autocompleteAddress.address_components.find((obj) =>
          obj.types.includes("administrative_area_level_2")
        )
      : localAddress.inNYC;
    var stateObject = this.state.autocompleteAddress.address_components
      ? this.state.autocompleteAddress.address_components.find((obj) =>
          obj.types.includes("administrative_area_level_1")
        )
      : localAddress.state;
    var cityObject = this.state.autocompleteAddress.address_components
      ? this.state.autocompleteAddress.address_components.find((obj) =>
          obj.types.includes("locality")
        ) ||
        this.state.autocompleteAddress.address_components.find((obj) =>
          obj.types.includes("sublocality")
        )
      : localAddress.city;
    if (!stateObject) {
      alert("You must search by a precise location");
    } else {
      var state =
        stateObject && stateObject.short_name ? stateObject.short_name : "";
      var city = cityObject && cityObject.long_name ? cityObject.long_name : "";
      var inNYC = "";
      if (inNYCObject) {
        inNYC = inNYCObject.long_name;
      }
      var addressObject = {
        address: this.state.autocompleteAddress
          ? this.state.autocompleteAddress.formatted_address
          : this.state.initialAddress,
        longitude: longitude,
        latitude: latitude,
        state: stateObject,
        city: cityObject,
        inNYC: inNYCObject,
      };

      if (this.state.timeFrame === 6) {
        sessionStorage.setItem("qts_range_a", 0);
        sessionStorage.setItem("qts_range_b", 0);
        sessionStorage.setItem("qts_range_c", 40);
      }
      if (this.state.timeFrame === 12) {
        sessionStorage.setItem("qts_range_a", 30);
        sessionStorage.setItem("qts_range_b", 30);
        sessionStorage.setItem("qts_range_c", 60);
      }
      if (this.state.timeFrame === 18) {
        sessionStorage.setItem("qts_range_a", 65);
        sessionStorage.setItem("qts_range_b", 65);
        sessionStorage.setItem("qts_range_c", 80);
      }
      if (this.state.timeFrame === 24) {
        sessionStorage.setItem("qts_range_a", 100);
        sessionStorage.setItem("qts_range_b", 100);
        sessionStorage.setItem("qts_range_c", 100);
      }
      sessionStorage.setItem("qts_show_inactive", 0);
      sessionStorage.setItem(
        "qts_propertyTypes",
        this.state.property_types.join(",")
      );
      sessionStorage.setItem("qts_address", JSON.stringify(addressObject));
      sessionStorage.setItem("qts_min_loan", this.state.min_loan);
      sessionStorage.setItem("qts_max_loan", this.state.max_loan);
      sessionStorage.setItem("qts_radius", this.state.radius);
      sessionStorage.setItem("qts_timeFrame", this.state.timeFrame);
      sessionStorage.setItem(
        "qts_bankNote",
        this.state.bankNote ? this.formatBankNote(this.state.bankNote) : ""
      );
      sessionStorage.setItem("qts_searchType", this.state.searchType);
      sessionStorage.setItem("qts_bank", this.state.bank);
      sessionStorage.setItem("qts_banker", this.state.banker);
      sessionStorage.setItem("qts_lending", this.state.lending_types.join(","));

      if (
        inNYC === "New York County" ||
        inNYC === "Bronx County" ||
        inNYC === "Kings County" ||
        inNYC === "Queens County" ||
        inNYC === "Richmond County"
      ) {
        sessionStorage.setItem("qts_in_nyc", 1);
      } else {
        sessionStorage.setItem("qts_in_nyc", 0);
      }

      var data = {
        latitude: latitude,
        longitude: longitude,
        state: state,
        city: this.state.radius === "city" ? city : "",
        property_types: this.state.property_types.length
          ? encodeURIComponent(this.state.property_types)
          : "",
        radius: this.state.radius === "city" ? "" : this.state.radius,
        min_loan: this.state.min_loan,
        max_loan: this.state.max_loan,
        date_range: this.state.timeFrame,
        bank: this.state.bank,
        banker: this.state.banker,
        lending_types: this.state.lending_types,
        note_search: this.state.bankNote,
        search_type: this.state.searchType,
        address: this.state.initialAddress,
        in_nyc:
          inNYC === "New York County" ||
          inNYC === "Bronx County" ||
          inNYC === "Kings County" ||
          inNYC === "Queens County" ||
          inNYC === "Richmond County"
            ? 1
            : 0,
      };
      Object.keys(data).forEach(
        (key) => (data[key] === null || data[key] === "") && delete data[key]
      );
      Navigation.go(`/qts-banks?${encodeQueryData(data)}`);
    }
  }

  render() {
    return (
      <div className="qts-search-form mui--z1">
        <Row>
          <h3>Ask QTS</h3>
        </Row>
        <Row className="qts-address-bar">
          <i className="material-icons">search</i>
          <AutocompleteSearch
            value={this.state.initialAddress}
            placeholder={this.state.radius === "city" ? "City" : "Address"}
            onChange={this.handleAddressChange}
            onlyValidAddress={false}
          />
        </Row>
        <Row className="qts-search-second-row">
          <Col
            md="12"
            sm="12"
            xs="12"
            className="qts-property-box"
            onClick={this.propertyToggleDropdown}
          >
            <i className="material-icons">business</i>
            <MultiSelect
              options={this.state.localPropertyTypes}
              selectedOptions={this.state.property_types}
              placeholder="Property Type (optional)"
              showDropdown={this.state.propertyToggleDropdown}
              checkboxChange={this.propertyTypeCheckboxChange}
            />
          </Col>
        </Row>
        <Row>
          <p
            onClick={this.showAdvancedSearch}
            className={
              this.state.toggleAdvancedSearch
                ? "hidden"
                : "orange-text toggle-search"
            }
          >
            Advanced Search
          </p>
          <p
            onClick={this.showAdvancedSearch}
            className={
              this.state.toggleAdvancedSearch
                ? "orange-text toggle-search"
                : "hidden"
            }
          >
            Hide Advanced Search
          </p>
        </Row>
        {this.state.toggleAdvancedSearch && (
          <>
            <Row className="qts-min-max-loan">
              <Col md="6" sm="6" xs="12">
                <div className="qts-loan-box">
                  <i className="material-icons">attach_money</i>
                  <input
                    className="input"
                    onChange={this.formatMinLoan}
                    value={this.state.min_loan}
                    placeholder="Min Loan Amount"
                  />
                </div>
              </Col>
              <Col md="6" sm="6" xs="12">
                <div className="qts-loan-box">
                  <i className="material-icons">attach_money</i>
                  <input
                    className="input"
                    onChange={this.formatMaxLoan}
                    value={this.state.max_loan}
                    placeholder="Max Loan Amount"
                  />
                </div>
              </Col>
            </Row>
          <Row className="qts-search-second-row">
            <Col md="6" sm="6" xs="12">
              <div className="qts-search-area">
                <i className="material-icons location-icon">place</i>
                <Select
                  value={this.state.radius ? this.state.radius.toString() : ""}
                  onChange={this.radiusChange}
                  className="search-area-select"
                >
                  <Option value="0.25" label="1/4 Mile" />
                  <Option value="0.5" label="1/2 Mile" />
                  <Option value="1" label="1 Mile" />
                  <Option value="3" label="3 Miles" />
                  <Option value="5" label="5 Miles" />
                  <Option value="city" label="Entire City" />
                  <Option value="10" label="10 Miles" />
                  <Option value="25" label="25 Miles" />
                  <Option value="50" label="50 Miles" />
                  <Option value="60" label="60 Miles" />
                  {/*<Option value="state" label="Entire State" />*/}
                </Select>
              </div>
            </Col>
            <Col md="6" sm="6" xs="12">
              <div className="qts-time-frame">
                <i className="material-icons">access_time</i>
                <Select
                  value={this.state.timeFrame}
                  onChange={this.timeFrameChange.bind(this)}
                  className="time-frame-select"
                >
                  <Option value="6" label="6 Months" />
                  <Option value="12" label="12 Months" />
                  <Option value="18" label="18 Months" />
                  <Option value="24" label="24 Months" />
                </Select>
              </div>
            </Col>
          </Row>
            <Row className="qts-show-result-row">
              <div
                className="qts-search-area"
                style={{ marginRight: 0 }}
                onClick={this.lenderToggleDropdown}
              >
                <i className="material-icons">assessment</i>
                <MultiSelect
                  options={this.props.lendingTypesList}
                  selectedOptions={this.state.lending_types}
                  placeholder="Lending Program"
                  showDropdown={this.state.lenderToggleDropdown}
                  checkboxChange={this.lenderTypeCheckboxChange.bind(this)}
                />
              </div>
            </Row>
            <Row className="qts-show-result-row">
              <div className="qts-loan-box" style={{ width: "100%" }}>
                <i className="material-icons">account_balance</i>
                <input
                  className="input"
                  onChange={this.formatBank.bind(this)}
                  value={this.state.bank}
                  placeholder="Bank"
                />
              </div>
            </Row>
            <Row className="qts-show-result-row">
              <div className="qts-loan-box" style={{ width: "100%" }}>
                <i className="material-icons">person</i>
                <input
                  className="input"
                  onChange={this.formatBanker.bind(this)}
                  value={this.state.banker}
                  placeholder="Banker"
                />
              </div>
            </Row>
            <Row className="qts-show-result-row">
              <div className="qts-bank-note" style={{ width: "100%" }}>
                <img
                  className="bank-note-icon"
                  src="/images/icons/qts-note.png"
                  alt="bank-note-ico"
                />
                <input
                  value={this.state.bankNote}
                  onChange={this.bankNoteChange.bind(this)}
                  className="input"
                  placeholder="Bank Note"
                />
              </div>
            </Row>
            <Row className="qts-show-result-row">
              <div className="qts-show-results">
                <i className="material-icons">filter_list</i>
                <Select
                  className="search-area-select"
                  value={this.state.searchType}
                  onChange={this.searchTypeChange.bind(this)}
                >
                  {!this.state.lending_types.length &&
                    <Option
                      value=""
                      label="Show Results From"
                      className="lc-disabled-option"
                    />
                  }
                  <Option value="ltr" label="Loan Tracker" />
                  {!this.state.lending_types.length &&
                    <Option value="national" label="Public Data"/>
                  }
                </Select>
              </div>
            </Row>
          </>
        )}
        <Row className="qts-search-btn-row">
          <Button
            disabled={
              !(
                this.state.initialAddress ||
                sessionStorage.getItem("qts_address")
              )
            }
            onClick={() => this.submitSearch()}
            className="qts-search-btn"
          >
            Search
          </Button>
        </Row>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    title: state.navTitle,
    qts: state.QTS,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    navActions: bindActionCreators(navigationTitleActions, dispatch),
    qtsActions: bindActionCreators(qtsActions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(QTSSearchForm);
