import React from "react";
import MultiSelectCheckbox from "./MultiSelectCheckbox";

class MultiSelect extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      localOptionsSelected: [],
    };

    this.checkboxChange = this.checkboxChange.bind(this);
    this.getMultiSelectTextValue = this.getMultiSelectTextValue.bind(this);
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (
      nextProps.selectedOptions &&
      nextProps.selectedOptions !== this.state.localOptionsSelected
    ) {
      this.setState({ localOptionsSelected: nextProps.selectedOptions });
    }
  }

  checkboxChange(id, isChecked) {
    if (id === 0 && isChecked) {
      this.setState(
        {
          localOptionsSelected: this.props.options
            .slice(1)
            .map((p) => p.list_type_id),
        },
        () => this.props.checkboxChange(this.state.localOptionsSelected)
      );
    } else if (id === 0 && !isChecked) {
      this.setState({ localOptionsSelected: [] }, () =>
        this.props.checkboxChange(this.state.localOptionsSelected)
      );
    } else if (isChecked) {
      this.setState(
        { localOptionsSelected: [...this.state.localOptionsSelected, id] },
        () => this.props.checkboxChange(this.state.localOptionsSelected)
      );
    } else if (!isChecked) {
      this.setState(
        {
          localOptionsSelected: this.state.localOptionsSelected.filter(
            (type) => parseInt(type, 10) !== id
          ),
        },
        () => this.props.checkboxChange(this.state.localOptionsSelected)
      );
    }
  }

  getMultiSelectTextValue() {
    let textValue = "";
    this.props.selectedOptions &&
      this.props.selectedOptions.map((value, index) => {
        let type = this.props.options.find((t) => t.list_type_id === value);
        if (type) {
          if (index >= 1) {
            textValue += ", ";
          }
          textValue += type.value;
        }
        return textValue;
      });
    return textValue.length > 0 ? textValue : this.props.placeholder;
  }

  render() {
    return (
      <div>
        {this.props.placeholder ? (
          <p className="qts-multiselect-placeholder">
            {this.getMultiSelectTextValue()}
          </p>
        ) : (
          ""
        )}
        {/*<i className="material-icons drop-arrow">arrow_drop_down</i>*/}
        <div
          className={
            this.props.showDropdown ? "qts-dropdown-box mui--z1" : "hidden"
          }
        >
          {this.props.options &&
            this.props.options.map((item, index) => (
              <MultiSelectCheckbox
                key={index}
                id={item.list_type_id}
                item={item}
                checked={
                  item.list_type_id === 0
                    ? this.state.localOptionsSelected.length ===
                      this.props.options.filter((o) => o.list_type_id !== 0)
                        .length
                    : this.state.localOptionsSelected.find(
                        (t) => parseInt(t, 10) === item.list_type_id
                      )
                }
                onChangeHandler={this.checkboxChange}
              />
            ))}
        </div>
      </div>
    );
  }
}

export default MultiSelect;
