import React from "react";

class MultiSelectCheckbox extends React.Component {
  render() {
    let item = this.props.item;
    return (
      <div className="rkmd-checkbox checkbox-ripple">
        <label className="input-checkbox checkbox-lightBlue">
          <input
            type="checkbox"
            id={this.props.id}
            checked={this.props.checked || false}
            onChange={this.props.onChangeHandler.bind(
              null,
              this.props.id,
              !this.props.checked
            )}
          />
          <span className="checkbox"></span>
          <span className="ripple"></span>
        </label>
        <p
          htmlFor={this.props.id}
          className="label"
          onClick={this.props.onChangeHandler.bind(
            null,
            this.props.id,
            !this.props.checked
          )}
        >
          {item.value}
        </p>
      </div>
    );
  }
}
export default MultiSelectCheckbox;
