import React from "react";
const google = window.google;

class AutocompleteSearch extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      initialAddress: "",
    };

    this.handleAddressChange = this.handleAddressChange.bind(this);
  }

  componentDidMount() {
    this.loadGoogleAutocomplete();

    this.setState({ initialAddress: this.props.value });
  }

  componentDidUpdate() {
    this.loadGoogleAutocomplete();
  }

  loadGoogleAutocomplete() {
    let options = {
      componentRestrictions: { country: "us" },
    };
    if (this.props.onlyValidAddress) {
      options.types = ["address"];
    }
    var searchbox = new google.maps.places.Autocomplete(
      this.refs.autocomplete,
      options
    );
    searchbox.addListener(
      "place_changed",
      function () {
        this.refs.autocomplete.blur();
        this.place = searchbox.getPlace();
        if (this.place.geometry) {
          try {
            this.setState(
              {
                autocompleteAddress: this.place,
                initialAddress: this.place.formatted_address,
              },
              () => this.props.onChange(this.state.autocompleteAddress)
            );
          } catch (err) {
            alert(err);
          }
        } else {
          alert("there was a problem with google autocomplete");
        }
      }.bind(this)
    );
  }

  handleAddressChange(e) {
    if (e.keyCode === 13) {
      e.preventDefault();
    }
    this.setState({
      initialAddress: e.target.value,
    });
  }

  render() {
    return (
      <input
        className="input"
        type="text"
        ref="autocomplete"
        value={this.state.initialAddress}
        onChange={this.handleAddressChange}
        placeholder={this.props.placeholder}
        style={{ color: "#000" }}
      />
    );
  }
}

export default AutocompleteSearch;
