import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as navigationTitleActions from "../../actions/navigationTitleActions";
import * as qtsActions from "../../actions/qtsActions";
import QTSSearchForm from "./QTSSearchForm";
import Modal from "react-modal";

class AskQTSSearchContainer extends React.Component {
  async componentDidMount() {
    this.props.navActions.updateNavTitle("Ask QTS");
    await this.props.qtsActions.getLendingTypeList();
    await this.props.qtsActions.getPropertyTypes();
  }

  render() {
    return (
      <div>
        {this.props.qts.lendingTypeList &&
        this.props.qts.propertyTypeList.length ? (
          <QTSSearchForm
            lendingTypesList={this.props.qts.lendingTypeList}
            propertyTypesList={this.props.qts.propertyTypeList}
          />
        ) : (
          <Modal
            className="loading-modal"
            shouldCloseOnOverlayClick={false}
            ariaHideApp={false}
            contentLabel="modal"
            isOpen={true}
          >
            <div className="centered-spinner">
              <img
                src="/images/icons/spin.svg"
                className="loading-spinner"
                alt="loading spinner"
              />
            </div>
          </Modal>
        )}
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    title: state.navTitle,
    qts: state.QTS,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    navActions: bindActionCreators(navigationTitleActions, dispatch),
    qtsActions: bindActionCreators(qtsActions, dispatch),
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(AskQTSSearchContainer);
